:root{
  --btn-primary: #3d5af1;
  --box: #233345;
  --background: #364f6b;
  --text-light: #fff;
  --default-font-family: 'Poppins';

}

*{
  margin: 0;
  padding: 0;
  background: var(--background);
  font-family: var(--default-font-family);
  color: var(--box);
  font-weight: 500px

}

.generate-text{
  position: absolute;
width: 1155px;
height: 24px;
left: 418px;
top: 702px;

font-family: Poppins;
font-style: normal;
font-weight: 600;
font-size: 16px;
line-height: 24px;
/* identical to box height */


color: #273749;
}

.navlinks{
  padding: 18px;
  margin-left: 1380px;
  text-decoration: none;
  width: fit-content;
  white-space: nowrap;
  font-weight: 500;
  margin-top: 30px;
}

.navlinks a{
  padding: 0.5rem;
  text-decoration: none;
  color: lightgrey;
  margin-right: 23px;
}

.navlinks a:hover{
  border-bottom: 2px solid black;
  transition: 1s ease;
}


.color-palette{
  margin-top: 400px;
  margin-left: 844px;
  padding: 14px;
  border: 4px solid #8d8e8f;
  width: fit-content;
  border-radius: 8px;
  color: #8d8e8f;
}
.nav-title{
  position: absolute;
  width: 74px;
  height: 35px;
  left: 33px;
  top: 24px;

  font-family: Pushster;
  font-style: normal;
  font-weight: normal;
  font-size: 25px;
  line-height: 35px;
/* identical to box height */


color: #121922;
}
.heading-title{
  position: absolute;
  width: 610px;
  height: 72px;
  left: 690px;
  top: 104px;
  font-family: var(--default-font-family);
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 72px;
  color: var(--text-light);
}

.color-container{
  position: absolute;
  width: 70%;
  height: 191px;
  left: 330px;
  top: 219px;
  background: var(--box);
  border-radius: 14px;
  transition: 0.4s ease-in-out;
  
}

.color-random-container{
  position: absolute;
  width: 587px;
  height: 191px;
  left: 630px;
  top: 219px;
  background: var(--box);
  border-radius: 14px;
  transition: 0.4s ease-in-out;
  
}

.buttons{
  margin-top: 100px;
}

.buttons .color-btn{
  position: absolute;
  width: 240px;
  height: 75px;
  left: 421px;
  top: 515px;
  font-weight: 600;
  border: 5px solid #263444;
  box-sizing: border-box;
  border-radius: 7px;
  outline: none;

}

.buttons .opacity-btn{
  position: absolute;
  width: 193px;
  height: 75px;
  left: 722px;
  top: 515px;
  font-weight: 600;
  border: 5px solid #263444;
  box-sizing: border-box;
  border-radius: 7px;
  outline: none;
}

.buttons .filter-btn{
  position: absolute;
  width: 151px;
  height: 75px;
  left: 976px;
  top: 515px;
  font-weight: 600;
  border: 5px solid #263444;
  box-sizing: border-box;
  border-radius: 7px;
  outline: none;
}

.buttons .gradient-btn{
  position: absolute;
  width: 322px;
  height: 75px;
  left: 1188px;
  top: 515px;
  font-weight: 600;
  border: 5px solid #263444;
  box-sizing: border-box;
  border-radius: 7px;
  outline: none;
}

.buttons .gradient-btn:focus{
  position: absolute;
  width: 402px;
  height: 75px;
  left: 1188px;
  top: 515px;
  font-weight: 600;
  border: 5px solid #263444;
  box-sizing: border-box;
  border-radius: 7px;
  filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.25));
  outline: none;
  transition: 0.4s ease-in-out;
}

.home{
  margin-left: 100px;
}

.home-section{
  font-size: 45px;
  margin-top: 300px;
  margin-left: 800px;
  color: #1f2a36;
}

.home-description{
  margin-left: 753px;
  margin-top: 100px;
  font-weight: 600;
}

.navlink{
  position: absolute;
width: 275px;
height: 27px;
left: 1599px;
top: 30px;
white-space: nowrap;
font-family: Poppins;
font-style: normal;
font-weight: 600;
font-size: 18px;
line-height: 27px;
display: flex;
text-decoration: none;
/* identical to box height */

letter-spacing: 0.12em;

color: #263444;

}
.generate-btn{
  position: absolute;
  width: 248px;
  height: 75px;
  left: 794px;
  top: 650px;
  font-size: 16px;
  font-weight: 600;
  border: 5px solid #263444;
  box-sizing: border-box;
  /* filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.25)); */
  border-radius: 7px;
}

.buttons .filter-btn:focus, .buttons .opacity-btn:focus, .buttons .color-btn:focus{
  filter: drop-shadow(0px 2px 4px rgba(0, 0, 0, 0.25));
  transition: 0.4s ease-in-out;

}
input{
  padding: 12px;
  border: none;
  font-size: 14px;
  text-align: center;
}

input:active{
  border: none;
}

input::placeholder{
  color: var(--box);
}

input:checked{
  background-color: red;
  padding: 122px;
}
input:focus{
  border: none;
  border: 2px solid var(--box);
}

.details-container{
  position: absolute;
  width: 587px;
  height: 61px;
  left: 630px;
  top: 407px;
  background: var(--box);
  border-radius: 7px;
  color: white;
}


